import React from "react";
import "./App.css";
import Direct from "./Components/Direct";
import Form from "./Components/Form";
import Todo from "./Components/Todo";

export default class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = { todos: [] };
  }

  componentDidMount() {
    const lastTodos = JSON.parse(localStorage.getItem("todos"));
    if (lastTodos != null) {
      this.setState({ todos: lastTodos });
    }

    console.log(lastTodos);
  }

  addTodo = (todo) => {
    const newTodos = [todo, ...this.state.todos];
    localStorage.setItem("todos", JSON.stringify(newTodos));
    this.setState({ todos: newTodos });
  };

  deleteTodo = (id) => {
    const updateTodos = this.state.todos.filter((i) => i.id !== id);
    localStorage.setItem("todos", JSON.stringify(updateTodos));
    this.setState({ todos: updateTodos });
  };

  render() {
    return (
      <div className="App">
        <div className="Wrapper">
          <div className="Header">
            <h1>Tareas</h1>
            <Form addTodo={this.addTodo}></Form>
          </div>
          <Todo todos={this.state.todos} deleteTodo={this.deleteTodo}></Todo>
        </div>
        <Direct></Direct>
      </div>
    );
  }
}
