import React, { Component } from "react";

export default class Direct extends Component {
  constructor(props) {
    super(props);
    this.state = {
      access: [
        { value: "Correo", link: "https://outlook.office365.com/mail/inbox" },
        { value: "Sigaa", link: "https://sigaa.upb.edu.co/ssomanager/c/SSB" },
        { value: "Aula Digital", link: "https://auladigital.upb.edu.co/" },
      ],
    };
  }
  render() {
    return (
      <div className="Direct">
        <ul>
          {this.state.access.map((i) => (
            <li>
              <a href={i.link} target="_blank">
                {i.value}
              </a>
            </li>
          ))}
        </ul>
      </div>
    );
  }
}
